// card.js
export default function card({ img, description, buttonText }) {
  return (
    <div className='flex justify-center items-baseline-last py-10'>
      <div className="bg-white border border-black p-4 box-border w-[300px] rounded-2xl">
        <img
          src={img}
          alt="Product"
          className="max-w-full max-h-full object-contain mb-2"
        />
        <center>
          <p className="inline text-black">{description}</p>
          <button className='bg-black text-white rounded-3xl py-2 mt-4'>
            <span className='hover:bg-red-500 text-white cursor-pointer rounded-3xl py-2.5'> 
             <center> {buttonText}</center>
            </span>
          </button>
        </center>
      </div>
    </div>
  );
}
